































import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import { LoadingScreen } from "@/mixins/loadingScreen";
import { routerModule, repositoriesModule } from "@/store/modules";
import Timeline from "@/components/Timeline.vue";
import SnapshotsList from "@/components/repositories/SnapshotsList.vue";
import { ISnapshot } from "@/store/api";
import { parse } from "date-fns";

@Component({
  components: {
    Timeline,
    SnapshotsList
  }
})
export default class RepositorySnapshotsView extends Mixins(LoadingScreen) {
  get repoId() {
    return Number(routerModule.params.repositoryId);
  }

  async mounted() {
    this.setLoading(true);
    const response = await repositoriesModule.listSnapshots(this.repoId);
    if (response.payload) {
      this.snapshots = response.payload.snapshots;
    }
    this.setLoading(false);
  }

  snapshots: ISnapshot[] = [];
}
